// Fetch wrapper with retry logic and error handling
window.fetchWithRetry = async function(url, options = {}, retryOptions = {}) {
	const {
		maxRetries = 2,
		retryDelay = 1000,
		timeout = 30000,
		onRetry = null
	} = retryOptions;
	
	const controller = new AbortController();
	const timeoutId = setTimeout(() => controller.abort(), timeout);
	
	const fetchOptions = {
		...options,
		signal: controller.signal
	};
	
	async function attemptFetch(attempt) {
		try {
			const response = await fetch(url, fetchOptions);
			clearTimeout(timeoutId);
			
			if (!response.ok && attempt < maxRetries) {
				// Retry on 5xx errors or specific 4xx errors
				if (response.status >= 500 || response.status === 408 || response.status === 429) {
					throw new Error(`HTTP ${response.status}: ${response.statusText}`);
				}
			}
			
			return response;
		} catch (error) {
			clearTimeout(timeoutId);
			
			// Don't retry on client errors (except timeout/rate limit)
			if (error.name === 'AbortError') {
				throw new Error('Request timeout');
			}
			
			if (attempt < maxRetries) {
				if (onRetry) {
					onRetry(attempt + 1, error);
				}
				
				// Exponential backoff
				const delay = retryDelay * Math.pow(2, attempt);
				await new Promise(resolve => setTimeout(resolve, delay));
				
				return attemptFetch(attempt + 1);
			}
			
			throw error;
		}
	}
	
	return attemptFetch(0);
};

// Enhanced error logging for fetch failures
window.logFetchError = function(url, error) {
	const errorInfo = {
		url: url,
		error: error.message,
		timestamp: new Date().toISOString(),
		userAgent: navigator.userAgent
	};
	
	console.error('Fetch failed:', errorInfo);
	
	// Report to Sentry if available
	if (window.Sentry !== undefined) {
		window.Sentry.captureException(error, {
			extra: errorInfo,
			tags: {
				component: 'fetch'
			}
		});
	}
	
	return errorInfo;
};

// Graceful image loading with fallback
window.loadImageWithFallback = function(img, fallbackSrc) {
	const originalSrc = img.src;
	
	img.onerror = function() {
		if (img.src !== fallbackSrc) {
			console.warn(`Failed to load image: ${originalSrc}, using fallback`);
			img.src = fallbackSrc;
		}
	};
	
	// Retry original image after delay
	img.onload = function() {
		if (img.src === fallbackSrc) {
			setTimeout(() => {
				const testImg = new Image();
				testImg.onload = () => {
					img.src = originalSrc;
				};
				testImg.src = originalSrc;
			}, 30000); // Retry after 30 seconds
		}
	};
};
(() => {
    const BREAKPOINT = 768.0;

    let timer;
    let previousSize = window.innerWidth;

    function didResize() {
        const nextSize = window.innerWidth;

        if (previousSize < BREAKPOINT && nextSize < BREAKPOINT) {
            previousSize = nextSize;
            return;
        }

        if (previousSize > BREAKPOINT && nextSize > BREAKPOINT) {
            previousSize = nextSize;
            return;
        }

        previousSize = nextSize;

        clearTimeout(timer);
        timer = setTimeout(didResizeThrottled, 40);
    }

    function didResizeThrottled() {
        document.querySelectorAll("img[srcset]").forEach((image) => {
            const srcset = image.srcset;
            image.removeAttribute("srcset");
            setTimeout(() => {
                image.setAttribute("srcset", srcset);
            }, 0);
        });
    }

    window.addEventListener("resize", didResize);
})();
function loadStyles(url) {
    function destroyStyles() {}

    return new Promise((resolve) => {
        const existing = document.querySelector(`link[href="${url}"]`);

        if (existing) {
            resolve(destroyStyles);
            return;
        }

        const link = document.createElement("link");

        link.onload = function didLoadStyles() {
            resolve(destroyStyles);
        };

        link.rel = "stylesheet";
        link.href = url;

        document.querySelector("head").appendChild(link);
    });
};
