function createPressHighlights(element) {
	if (element === undefined || element === null) {
		return function noop() {};
	}

	element.dataset.init = "true";

	const itemsElement = element.querySelector(".press-highlights-items");

	if (itemsElement === undefined || itemsElement === null) {
		return function noop() {};
	}

	const scrollButtons = element.querySelectorAll(
		".press-highlights-scroll-button"
	);

	if (scrollButtons.length < 2) {
		return function noop() {};
	}

	const [scrollPreviousButton, scrollNextButton] = scrollButtons;

	const spacerElement = element.querySelector(
		".press-highlights-scroll-spacer"
	);
	if (spacerElement === undefined || spacerElement === null) {
		return function noop() {};
	}

	function didClickPreviousButton() {
		const items = Array.from(
			itemsElement.querySelectorAll(".press-highlights-scroll-item") || []
		);

		if (items.length === 0) {
			return;
		}

		const previousItem = items.findLast((item) => {
			const rect = item.getBoundingClientRect();
			return rect.left < 0;
		});

		if (previousItem === undefined) {
			return;
		}

		const nextElement = previousItem.nextElementSibling;

		if (nextElement === undefined || nextElement === null) {
			return;
		}

		let gutterSize = 0;
		const prevRect = previousItem.getBoundingClientRect();
		const nextRect = nextElement.getBoundingClientRect();

		if (prevRect !== undefined && nextRect !== undefined) {
			gutterSize = nextRect.left - prevRect.right;
		}

		const scrollLeft =
			items[0] === previousItem || items[1] === previousItem
				? 0.0
				: itemsElement.scrollLeft -
					previousItem.clientWidth -
					gutterSize;

		itemsElement.scrollTo({
			left: scrollLeft,
			behavior: "smooth"
		});
	}

	function didClickNextButton() {
		const items = Array.from(
			itemsElement.querySelectorAll(".press-highlights-scroll-item") || []
		);

		if (items.length === 0) {
			return;
		}

		const viewportWidth = window.innerWidth;

		const nextItem = items.find((item) => {
			const rect = item.getBoundingClientRect();
			return rect.right > viewportWidth;
		});

		if (nextItem === undefined) {
			return;
		}

		const previousElement = nextItem.previousElementSibling;

		if (previousElement === undefined || previousElement === null) {
			return;
		}

		let gutterSize = 0;
		const nextRect = nextItem.getBoundingClientRect();
		const prevRect = previousElement.getBoundingClientRect();

		if (nextRect !== undefined && prevRect !== undefined) {
			gutterSize = nextRect.left - prevRect.right;
		}

		const lastItem = items[items.length - 1];
		const secondLastItem = items[items.length - 2];

		const scrollLeft =
			(lastItem === nextItem || secondLastItem === nextItem) &&
			lastItem !== undefined
				? itemsElement.scrollWidth - itemsElement.clientWidth
				: itemsElement.scrollLeft + nextItem.clientWidth + gutterSize;

		itemsElement.scrollTo({
			left: scrollLeft,
			behavior: "smooth"
		});
	}

	function didChangeViewport() {
		if (
			scrollPreviousButton === undefined ||
			scrollNextButton === undefined ||
			spacerElement === undefined
		) {
			return;
		}

		scrollPreviousButton.classList.toggle(
			"press-highlights-scroll-button-visible",
			itemsElement.scrollLeft > window.innerWidth * 0.2
		);

		let spacerLeft = 0;
		const spacerRect = spacerElement.getBoundingClientRect();

		if (spacerRect !== undefined) {
			spacerLeft = spacerRect.left;
		}

		scrollNextButton.classList.toggle(
			"press-highlights-scroll-button-visible",
			spacerLeft > window.innerWidth * 1.1
		);
	}

	if (itemsElement !== undefined && itemsElement !== null) {
		itemsElement.addEventListener("scroll", didChangeViewport);
	}

	if (scrollPreviousButton !== undefined && scrollPreviousButton !== null) {
		scrollPreviousButton.addEventListener("click", didClickPreviousButton);
	}

	if (scrollNextButton !== undefined && scrollNextButton !== null) {
		scrollNextButton.addEventListener("click", didClickNextButton);
	}

	window.addEventListener("resize", didChangeViewport);

	didChangeViewport();

	return function destroyPressHighlights() {
		if (itemsElement !== undefined && itemsElement !== null) {
			itemsElement.removeEventListener("scroll", didChangeViewport);
		}

		if (
			scrollPreviousButton !== undefined &&
			scrollPreviousButton !== null
		) {
			scrollPreviousButton.removeEventListener(
				"click",
				didClickPreviousButton
			);
		}

		if (scrollNextButton !== undefined && scrollNextButton !== null) {
			scrollNextButton.removeEventListener("click", didClickNextButton);
		}

		window.removeEventListener("resize", didChangeViewport);

		if (element !== undefined && element !== null) {
			delete element.dataset.init;
		}
	};
}

